
@import 'style';

.item_wrap{
    @include clear; 
    margin: 0 auto;
}

.item_card {
    position: relative;
    width: 770px;
    margin-bottom: 100px;
    // float:left;
}

// reponsive
.item_card,
.grid-sizer { width: 48%; }
.grid-gutter {width: 4%}


.card{
    position: relative;
    display: block;
    color: #171a1c;

    .type{
        margin-bottom: 10px;
        font-family: 'Roboto', sans-serif;
        font-weight: 900;
        font-size: 1rem;
    }
    .title{
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 1.3;
    }
    .img{
        width:100%;
        height: 57%;
        margin-bottom: 40px;
        overflow: hidden;
        background-color: #eee;
        img{
            width:100%;
            transform:scale(1.1);
            transition: $speed;
        }
    }
    .content{
        margin-top: 20px;
    }

    &:hover{
        img{
            transform:scale(1);
        }
    }
}

.item_view{
    position: relative;
    margin-top: 50px;

    // position: fixed;
    // width: 100% !important;
    // height: 100%;
    // background: #fff;
    // z-index: 999;
    // top:0;
    // left:0;
    // overflow-y: auto;

    .inner{
        max-width: 1920px;
        margin: 0 auto;
        background-color: #fff;
    }

    .visual{
        // margin-bottom: 20px;
        img{
            width:100%;
        }
    }
    .desc{
        margin-bottom: 150px;
        word-break: keep-all;
    }
    .close{
        display: inline-block;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        top: 20px;
        right: 20px;
        background-color: rgba(0,0,0,.4);
        &:before,
        &:after{
            content: '';
            display: inline-block;
            position: absolute;
            width: 50%;
            height: 2px;
            top: 50%;
            left: 0;
            right: 0;
            margin: auto;
            border-radius: 5px;
            background-color: #fff;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }

}


.prinfo{
    position: relative;
    margin: 80px 0;
    @include clear;
    li{
        width: 25%;
        float: left;
        font-size: 1.17rem;
        
        .cate{
            margin-bottom: 10px;
            color: #8e8e8e;
        }
    }
}



.view_coltype {
    padding: 150px 100px 60px 0;
    max-width:1690px;
    text-align: justify;

    @include clear;
    > div{
        float:left;
    }
    .left{
        width:260px;
    }
    .right{
        width: calc(100% - 260px);
    }

    .tit {
        position: relative;
        margin-bottom: 50px;
        padding-left: 90px;
        font-weight: 900;
        font-size: 1.4rem;

        &:before{
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            background-color: #000;
            width: 70px;
            height: 2px;
        }
    }
    .desc {
        font-size: 1.5rem;
        line-height: 1.8;
    }

    &.coltype{
        > p{
            float: left;
        }
        .cate{
            display:inline-block;
            width: 260px;
        }
        .desc{
            width: calc(100% - 260px);
            font-size: 24px;
            line-height: 1.8;
        }
    }
}


.filter{
    margin: 130px 0 60px;

    .filter_tg{
        display: none;
    }
    
    .filters-button-group{
        button {
            min-height: inherit;
            padding: 0;
            margin-right: 50px;
            font-size: 1.25rem;
            font-family: 'Roboto', sans-serif;
            font-weight: 900;
            background: none;
            color: #000;
    
            &.on{
                text-decoration: underline;
            }
            &:last-child{
                margin: 0;
            }
        }
    }
}

.grid-item{
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
}


.loading_box{
    background-color: rgba(255,255,255,.9);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;

    span{
        display: inline-block;
        animation: loadMotion 1s ease-out infinite alternate;
    }
}

.type_view{
    .inner_left,
    .btn_wrap{
        max-width: 1920px;
        padding: 0 0 0 230px;
        margin-left: auto;   
        margin-right: auto;   
    }

    .content{
        padding-top:300px;
        margin-top:-300px;
        background-color: #f5f5f5;
    }

    img{
        max-width: 100%;
    }
}

@keyframes loadMotion{
    0%{
        opacity: 0.7;
        transform: scale(1);
    }
    100%{
        opacity: 1;
        transform: scale(1.1);
    }
}

@media (max-width: 1024px) {
    /* 5 columns for larger screens */
    .item_card,
    .grid-sizer { width: 100%; }
    .grid-gutter {width: 0;}

    .item_card {
        margin-bottom: 50px;
    }

    .prinfo{
        margin: 40px 0;
        li{
            width: 50%;
            margin-bottom: 20px;
        }
    }

    .view_coltype {
        padding: 50px 20px 20px 0;
        
        > div,
        .left,
        .right{
            float: inherit;
            width: 100%;
        }
    }

    .type_view {

        .btn_wrap{
            max-width:100%;
            padding: 0;
        }

        .content{
            padding-top:100px;
            margin-top:-100px;
        }
    }
}

@media (max-width:480px) {
    .filter{
        margin: 50px 0 20px;

        .filter_tg{
            display: block;
            position: relative;
            padding: 0;
            margin: 0 0 30px 0;
            min-height: inherit;
            font-size: 0.91rem;
            font-weight: 600;
            color: #000;

            .arrow{
                position: absolute;
                display: inline-block;
                width: 12px;
                height: 12px;
                right: -20px;
                top: 10px;
                transform: rotateX(-180deg);
                transform-origin: 0 0;

                &:after,
                &:before{
                    content: '';
                    display: block;
                    width: 6px;
                    height: 1px;
                    background-color: #000;
                    position: absolute;
                    top: 0;
                }

                &:after{
                    transform: rotate(-45deg);
                    right: 1px;
                }

                &:before{
                    transform: rotate(45deg);
                    left: 1px;
                }
            }

            &:after{
                content: '';
                display: block;
                width: 100%;
                padding-right: 20px;
                height: 2px;
                background-color: #000;
                position: absolute;
                bottom: -6px;
                left: 0; 
            }

            &:hover{
                background: none;
            }
        }
        .filters-button-group{
            height: 175px;
            overflow: hidden;
            // transition: $speed;

            button {
                display: block;
                margin: 0 0 20px 0;
                font-size: 0.91rem;
                font-weight: 600;
            }
        }

        &.fold{
            .filters-button-group{
                visibility: hidden;
                height: 0;
            }
            .arrow{
                transform: rotateX(0deg);
            }
        }
    }

    .card{
        .img{
            margin-bottom: 20px;
        }
        .type{
            margin-bottom: 7px;
            font-size: 0.7rem;
        }
        .title{
            font-size: 0.98rem;
        }
    }

    .item_card {
        margin-bottom: 50px;
    }
        
    .prinfo{
        li{
            width: 100%;
            float: inherit;
            font-size: .78rem;
        }
    }

    .item_view{
        .desc{
            margin-bottom: 20px;
        }
    }

    .view_coltype {

        .tit {
            margin-bottom: 20px;
            padding-left: 35px;
            font-size: .85rem;
            &:before{
                width: 25px;
            }
        }
        .desc {
            font-size: 1.1rem;
            line-height:1.5;
        }
    }


}

