
@import 'style';

/****************************/
/* contact                  */
/****************************/

$cntMar : 80px;

.contact_wrap{
    position: relative;
    background-color: #f5f5f5;
    margin-top: 240px;
    padding-bottom: 100px;
    .action{
        margin-left: $cntMar;
    }
}

.contact {
    padding: $cntMar;
    padding-bottom: 0;
}
.ipt_wrap {
    position: relative;
    margin-bottom: 50px;
    visibility: hidden;
    opacity: 0;
    transform: translateY(10px);
    transition: .3s;
    font-size: 1.375rem;
    font-weight: bold;

    @include clear;

    input[type=text]{
        height:auto;
        min-width:60px;
        margin: 0 10px;
        padding: 0;
        border: 0;
        vertical-align: bottom;
        text-indent: 0;
        color:#888;
        font-size: 1.375rem;
        font-weight: bold;
        border-bottom: 1px solid #000;
        outline:none;
        transition: 0s;
    }

    textarea {
        padding: 0;
        text-indent: 0;
        font-size: 1.375rem;
        color:#888;
        border:0;
        border-bottom:1px solid #888;
        font-weight: bold;
        outline:none;
    }

    .checkwrap {
        margin-top:100px;
        label{
            line-height: 1;
            span {
                font-size: 1.375rem;
                color:#000;
                position: relative;
                &:after{
                    content:'';
                    display:block;
                    position: absolute;
                    bottom: 3px;
                    left:0;
                    width:100%;
                    height:1px;
                    background-color: #000;
                }
            }
        }
    }

    &.default,
    &.show{
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
    &.entered{
        font-weight: bold;
        color:#888;

        input[type=text]{
            border-bottom: 1px solid #888;
        }

    }
    &.focus{
        color:#000;
        font-size: 1.875rem;
        * {
            font-weight: bold;
            color:#000;
            font-size: 1.875rem;
        }

        input[type=text],
        textarea {
            color:#000;
            font-size: 1.875rem;
        }
    }

    &.col2{
        > span {
            float:left;
            min-width: 130px;
        }
        > div,
        textarea{
            float:left;
            width: calc(100% - 130px);
        }
    }
        
    .file {
        .btnwrap{
            @include clear;
            margin-top: 5px;
        }
        label{
            display:table;
            float:left;
            span{
                display:table-cell;
                vertical-align: middle;
            }
        }
        .txt{
            float:left;
            margin-left: 10px;
            font-size: 1.25rem;
            color:#3c3c3c;
        }

        #uploadList{
            margin-top: 30px;
            .list {
                border-bottom:1px solid #bbb;
                margin-bottom:20px;
                font-size: 1.375rem;
                color:#000;
                @include clear;

                &:last-child{
                    margin-bottom: 0;
                }
        
                > span {
                    display:inline-block;
                    max-width: calc(100% - 30px);
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
        
                .close{
                    position: relative;
                    float:right;
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    min-height: 30px;
                    padding: 0;
                    &:before,
                    &:after{
                        content: '';
                        display: inline-block;
                        position: absolute;
                        width: 50%;
                        height: 1px;
                        top: 50%;
                        left: 0;
                        right: 0;
                        margin: auto;
                        background-color: #000;
                    }
                    &:before{
                        transform: rotate(45deg);
                    }
                    &:after{
                        transform: rotate(-45deg);
                    }
                }
                
            }
        }
    }

}

.fake{
    visibility:hidden;
    min-width:60px;
    position:absolute;
    right:0;
    border: 1px solid red;
    display: inline-block;
    font-weight: bold;
    color:#aaa;
    font-size: 1.375rem;

    &.focus{
        color:#000;
        font-size: 1.875rem;   
    }
}

.btn_next{
    transition:.2s;
}

.arrow{
    top:-150px;
    transform: scale(1.4);
}


@media (max-width:480px) {
    html{
        font-size:70%;
    }

    .ipt_wrap{

        &.col2{
            > span {
                float:inherit;
                min-width: 100%;
            }
            > div,
            textarea{
                float:inherit;
                width: 100%;
            }
        }

        .file {
            .btnwrap{
                margin-top: 10px;
            }
            label{
                float: inherit;
                width:100%;
                height:40px;
                margin-bottom: 10px;
            }
            .txt{
                float:inherit;
                margin-left: 0;
            }
        }

        .checkwrap {
            margin-top:50px;
        }
    }

    .contact_wrap{
        margin-top: 0;
        padding-bottom: 30px;
        .contact{
            padding:20px;
        }
        .action{
            position: fixed;
            z-index:10;
            bottom: 0;
            width: 100%;
            margin-left: 0;
        }
    }

    footer,
    .section_copy{
        display:none;
    }

}

