
@import 'style';

footer {
   position: relative;
   padding: 0 0 0 230px;

   ul {
      position: relative;
      // height: 130px;
      padding: 50px 0;
      li{
         display: inline-block;
         padding-left: 25px;
         margin-right: 45px;
         font-size: 14px;
         color: #3b3b3b;
         background-repeat: no-repeat;
         background-position: 0 center;

         &:last-child{
            margin: 0;
         }

         &.place{
            background-image: url('../images/common/footer-location-icon.png');
         }
         &.site{
            background-image: url('../images/common/footer-mail-icon.png');
         }
         &.tel{
            background-image: url('../images/common/footer-phone-icon.png');

            .line{
               display:inline-block;
               border-right: 1px solid #9c9c9c;
               padding-right: 7px;
               margin-right: 7px;
               line-height: 1;
            }
         }
      }
   }

   &:before{
      content:'';
      display:block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 200px;
      left: 0;
      background-color: #f5f5f5;
   }

   &.simple {
      margin-top: 100px;
      background-color: #f5f5f5;

      .banner{
         display:none;
      }

      &:before{
         display:none;
      }
   }
}

@media (max-width:1024px) {
   footer {
      padding: 0;

      ul {
         padding: 20px;

         li {
            display: block;
            margin: 0 0 15px 0;
            &.place{
               background-position: 0 5px;
            }
         }
      }
   }
}